@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 .root{
  background-image:url(https://images.unsplash.com/photo-1592252032050-34897f779223?ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mzh8fG1hcHN8ZW58MHx8MHx8&ixlib=rb-1.2.1&w=1000&q=80);
  background-size:cover;
 }
.App {
  text-align: center;
}
.wrapper {
    margin: 30px;
}
.logo {
    width: 200px;
}
.blue {
    color: rgb(56, 76, 255)
}
.pink {
    color: rgb(253, 74, 106);
}
.city-country{
    font-size: 11vw;
    font-weight: bold;
    margin-top: -40px;
    margin-bottom: -25px;
}
.greeting {
    font-size: 7vw;
}
.greeting-container {
    
}
body {
    margin: 0;
}
*{
    font-size: xx-large;
   
}

.wrapper{
    margin: 0;
    width: 100vw;
    height: 120vh;
    background-image:url(https://images.unsplash.com/photo-1592252032050-34897f779223?ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mzh8fG1hcHN8ZW58MHx8MHx8&ixlib=rb-1.2.1&w=1000&q=80);
    background-size:cover;
    background-position: center;
}

.second-bg{
    background-color: #edf7c796;
    margin: 0;
    height: 120vh;
}

.logo{
    margin-top: 2em;
    width: auto;
    height: 30vh;
}

.swoopInTop{
    -webkit-animation-duration: 2s !important;
            animation-duration: 2s !important;
}

.title{
    font-family: 'Pacifico', cursive;
    font-size: 100px;
    text-shadow: black;
    color: black;
    margin-top: -2em;
    font-weight: 900;
}

.driveInLeft {
    -webkit-animation-duration: 3s !important;
            animation-duration: 3s !important;
}



.input-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}


 .input-country, .input-city, .submit {
    width: 30%;
    margin: 1em;
    padding: 1em;
    border-radius: 20px;
    box-shadow: black 1px 2px 3px;
    text-align: center;
    background-color: #ace9fc;
}
.input-country{
    width:36%;
    border: solid 2px black ;
    text-align-last:center;
}

input:focus, textarea:focus, select:focus{
    outline: none;
    }

