.news{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    padding: 3px;
    margin: 3px;
    border-radius: 9px;
    border: solid 1px rgba(0, 0, 0, 0.466);
    background-color: rgb(255, 255, 255);
    box-shadow:2px 2px 2px rgba(5, 5, 5, 0.192);
    margin-top: 0.5em;
}

 .news-description{
    padding: 0.5rem;
    margin: 2px;
    text-align: left;
    font-size: 3.5vw;
}
.news-title{
    margin: 0;
    text-align: left;
    padding: 10px;
    font-weight: bold;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    background-color: rgb(255, 183, 183);
    font-size:4vw;
    box-shadow: 0px 1px 0px rgba(5, 5, 5, 0.192);
}
.no-news {
    padding: 10px;
    background-color: rgb(252, 235, 238);
    text-align: left;
    padding-left: 20px;
}

.news-icon{
    width: 50vh;
    max-width: 2em;
    max-width: 2.5em;
    padding: 20px;
}

.text-icon-wrapper{
    display: flex;
    justify-content: space-between;
}

.news-link{
    margin: auto;
}
.container {
  margin-bottom: 60px;
  margin-top: 60px;
}

.tab-text {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  white-space: nowrap;
  font-size: 4vw;
}

.tabs-container {
  display: flex;
  margin-top: 60px;
  width: 100%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  border-bottom: solid 5px rgb(0, 0, 0);
}

.tab-div {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  height: 80px;
  width: 100%;
  background-color: rgb(255, 100, 108);
  margin-bottom: -5px;
  margin-left: 1px;
  margin-right: 1px;
  padding-left: 15px;
  padding-right: 15px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  text-align: center;
  box-shadow: -2px -2px 2px rgba(53, 53, 53, 0.4);
  border-top: solid 2px rgb(0, 0, 0);
  border-right: solid 2px rgb(0, 0, 0);
  border-left: solid 2px rgb(0, 0, 0);
  border-bottom: solid 2px rgb(0, 0, 0);
  cursor: pointer;
}
.tab-div:hover {
  background-color: rgb(214, 240, 250);
}

.active{
  background-color: rgb(214, 240, 250);
  border-bottom: solid 2px rgb(214, 240, 250);
  box-shadow: -1px -1px 1px rgba(0, 0, 0, 0.4);
}

.main-components {
  background-color: rgb(214, 240, 250);
  padding: 10px;
  height: 100vh;
}
/* WEATHER STYLES */
.weather-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.day-weather {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    padding: 3px;
    margin: 3px;
    border-radius: 9px;
    border: solid 1px rgba(0, 0, 0, 0.466);
    background-color: rgb(255, 255, 255);
    box-shadow:2px 2px 2px rgba(5, 5, 5, 0.192);
    margin-top: 0.5em;
}
.weather-header, .weather-header-detail {
    padding: 0.2em;
    font-weight: bold;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    background-color: rgb(255, 183, 183);
    font-size: 3.5vw;
    text-align: left;
}
.weather-header-detail {
    font-weight: normal;
}
.weather-div {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: nowrap;
    padding: 1.5em;
}
.weather-detail, .weather-date {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    margin: 5px;
    height: 40px;
    width: 45px;
    text-align: center;
    font-size: 5vw;
    font-weight: 700;
}
.weather-date {
    font-size: 4vw;
}
.weather-icon{
    width: 300%;
    -webkit-filter: drop-shadow(2px 2px 2px #222);
    filter: drop-shadow(2px 2px 2px #222);
}
.bold {
  font-weight: bold;
}
h1 {
  font-size: 4vw;
  padding: 10px;
}
.new-total{
  font-size: 4vw;
}
.shrink {
  font-size: 3.2vw;
}
.health-container {
  margin-top: 15px;
}
.health-cases-div-container {
  background-color: white;
  border: solid 1px rgba(0, 0, 0, 0.466);
